@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700,900&display=swap&subset=latin-ext);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.bayram{
  font: 100;
}
*{margin:0;padding:0;font-family:'Montserrat', sans-serif}a{text-decoration:none}a:hover{text-decoration:none}header{display:flex;align-items:center;justify-content:space-between;padding-top:30px;margin-bottom:30px}header .logo{color:black;font-weight:900;font-size:16px}header nav a{color:black;font-size:13px;margin-right:15px;opacity:.7;-webkit-transition-duration:.5s;transition-duration:.5s;-webkit-transition-property:opacity, color;transition-property:opacity, color}header nav a.active{color:#007bff;opacity:1}header nav a.active:hover{color:#007bff}header nav a:hover{opacity:1;color:#007bff;-webkit-transition-duration:.5s;transition-duration:.5s;-webkit-transition-property:opacity, color;transition-property:opacity, color}header nav a:last-child{margin-right:0}table{table-layout:fixed}

