@import url('https://fonts.googleapis.com/css?family=Montserrat:400,700,900&display=swap&subset=latin-ext');

* {
    margin: 0;
    padding: 0;
    font-family: 'Montserrat', sans-serif;
}

a {
    text-decoration: none;

    &:hover {
        text-decoration: none;
    }
}

header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 30px;
    margin-bottom: 30px;

    .logo {
        color: black;
        font-weight: 900;
        font-size: 16px;
    }

    nav {
        a {
            color: black;
            font-size: 13px;
            margin-right: 15px;
            opacity: .7;
            transition-duration: .5s;
            transition-property: opacity, color;

            &.active {
                color: #007bff;
                opacity: 1;

                &:hover {
                    color: #007bff;
                }
            }

            &:hover {
                opacity: 1;
                color: #007bff;
                transition-duration: .5s;
                transition-property: opacity, color;
            }

            &:last-child {
                margin-right: 0;
            }
        }
    }
}

table {
    table-layout: fixed;
}